import React, { ReactElement, useEffect } from 'react'

function Payment(): ReactElement {
  useEffect(function replaceEffect() {
    const redirectUrl =
      process.env.NODE_ENV === 'production'
        ? 'https://seonghui.github.io/xc-40/'
        : 'http://localhost:4000/'
    const url =
      process.env.NODE_ENV === 'production'
        ? 'https://stellashyou.github.io/payment-popup/'
        : 'http://localhost:4040/'

    console.log(process.env.NODE_ENV)
    window.location.replace(`${url}payment-popup/?redirect-url=${redirectUrl}`)
  }, [])

  return <>테스트</>
}

export default Payment
